body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(/starwarsapi/static/media/starWars.54a2b1ca.jpg) no-repeat center fixed;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


@font-face {
  font-family: StarWars;
  src: url(/starwarsapi/static/media/Starjout.f72f509a.ttf) format("truetype");
}

@font-face {
  font-family: StarWars2;
  src: url(/starwarsapi/static/media/Starjedi.cc3019aa.ttf) format("truetype");
}


@font-face {
  font-family: Starjedi2;
  src: url(/starwarsapi/static/media/Starjedi.cc3019aa.ttf) format("truetype");
}



